<template lang="pug">
  div.new-friend
    hd-bar.messages-hd-bar( title-name="新朋友" )
      span( slot='sec-l', @click="$router.go(-1)")
        span.iconfont.icon-fanhui 
        span 联系人
      div( slot="sec-r" ) 
        span( @click="$router.push({name: 'addfriend'})") 添加
    .main 
      .friend-notice 
        .friend-notice__header 
          span 好友通知
        .user-list.friend-notice__user-list
          .user-list__item.user-list__item_bt( v-for='list in request_list')     
            .user-list__avatar 
            .user-list__info.center-info 
              .center-info__nickname {{list.user.nickname}}
              .center-info__notice  {{ list.isFrom ? '我是'+list.user.nickname: '已发送验证'}}
            .user-list__r-info
              span( v-if= "!list.isFrom") {{ list.acceptStatus === 1 ? '已同意': '等待验证' }}
              div.ui-btn( v-else, @click="agree(list.acceptStatus, list._id)") {{list.acceptStatus === 1 ? '已同意': '同意'}}
        .friend-notice__more 
          span 更多      

</template>
<script>
import hdBar from '@/components/hd-bar'
import { socketEmit } from '@/socket/socket'
export default {
  components: {hdBar},
  data() {
    return {
      request_list: [],
      account: ''
    }
  },
  created() {
    socketEmit('getFriendRequest').then(res => {
      console.log(res, 'data')
      this.account = res.data.account;
      this.request_list = this.exec(this.account, res.data.request_list);
    })
  },
  methods: {
    exec(account, list) {
      return list.map((item) => {
        // 添加人是自己则是来自对方的
        let fromReq = item.addPeople.account === account;
        return Object.assign(item, { user:fromReq? item.creater: item.addPeople, isFrom: fromReq });
      })
    },
    agree(acceptStatus, id) {
      if(acceptStatus === 0) {
        socketEmit('agreeFriend', {id: id }).then(res => {
          console.log(res)
        })
      }
    }
  },
  filters: {
    
  }
}
</script>
<style lang="stylus" scoped>
  .new-friend   
    padding-top $hd-bar-height 
  .user-list 
    background #ffffff
    .user-list__item 
      color #bcbcbc 
      display flex
      padding 0 .7rem 
      align-items: center
      height 5.2rem
      border-bottom 1px solid #f2f2f2
    .user-list__avatar 
      width 3rem 
      height 3rem 
      border-radius 50%
      margin-right .7rem
      img 
        border-radius 50%
        width 100% 
        height 100%  
    .user-list__info 
      width 15rem 
    .user-list__r-info 
      text-align right
      flex 1 
      font-size .75rem
  .friend-notice 
    .friend-notice__header
      padding .45rem .7rem 
      color #767779 
    .friend-notice__user-list
      .center-info
        .center-info__nickname
          font-size .1rem 
          color #000
        .center-info__notice 
          font-size .8rem 
        .center-info__origin  
          font-size .8rem
    .friend-notice__more  
      color #bcbcbc 
      padding .9rem 0 
      text-align center 
      border-bottom 1px solid #efefef
      background-color #ffffff
      line-height 1    

      



</style>





